.Item-wrapper {
    display: -ms-flexbox;
    display: flex;
    padding: 30px;
    -ms-flex-direction: row;
        flex-direction: row;
    -ms-flex-wrap: wrap;
        flex-wrap: wrap;
    -ms-flex-pack: justify;
        justify-content: space-between;
}
.Item-card {
    width: 48%;
    position: relative;
    margin-bottom: 30px;
    -webkit-box-shadow: 0 2px 5px rgba(0, 0, 0, 0.4) !important;
            box-shadow: 0 2px 5px rgba(0, 0, 0, 0.4) !important;
}
.Item-card-title {
	font-size: 20px;
    font-weight: 400;
    line-height: 1.5;
    margin: 0;
    color: #000;
}
.Item-card-text {
    line-height: 2;
    color: #2c2c2c !important;
    max-height: 145px;
    overflow: hidden;
    white-space: normal;
    -o-text-overflow: ellipsis;
       text-overflow: ellipsis;
    display: -webkit-box;
    -webkit-line-clamp: 4;
    font-size: 16px !important;
    letter-spacing: 1px;
    margin-bottom: 52px;
}
.Item-card-actions {
    position: absolute !important;
    bottom: 0;
    width: 100%;
    -webkit-box-sizing: border-box;
            box-sizing: border-box;
}


