.App {
  	text-align: center;
}

.App-header {
	text-align: center;
}

.App-link-logo {
	display: block;
    width: auto;
    margin: 0 auto;
    overflow: hidden;
    line-height: .8;
    position: relative;
	text-align: center;
	cursor: pointer;
}

.App-link-logo h2 {
	font-size: 120px;
	font-weight: normal;
	color: #40A9F2; 
	margin: 0 0 100px;
	font-family: RobotoThin;
}

.App-link-logo img {
	width: 100%;
	height: auto;
}

.App-intro {
  	font-size: large;
}

.scrolling .App-appbar {
	background: #1b39a8;
	z-index: 1000;
	-webkit-box-shadow: 0 1px 4px #333;
	        box-shadow: 0 1px 4px #333;
}

.scrolling .App-appbar button {
	color: #fff !important;
}

.App-appbar {
	display: -ms-flexbox;
	display: flex;
	-ms-flex-pack: justify;
	    justify-content: space-between;
	-ms-flex-align: center;
	    align-items: center;
	-webkit-box-shadow: none !important;
	        box-shadow: none !important;
	position: fixed;
	top: 0;
	right: 0;
	left: 0;
}

.App-appbar h1 {
	text-align: center;
	color: #ec407a !important;
}

.App-appmain {
	margin-top: 80px;
}

.App-appmain-logo {
	/* background-image: url(http://materialdesignblog.com/wp-content/uploads/2015/04/MaterialDesignBlogo.png); */
	background-size: cover;
	height: 240px;
	line-height: 240px;
	margin-bottom: 30px;
	font-family: RobotoThin;
	text-align: center;
	background: #1b39a8;
	color: #fff;
	font-size: 80px;
}

.App-content {
	width: 90%;
	max-width: 1600px;
	margin: 0 auto;
}

.App-footer {
	text-align: center;
	background: #000;
	color: #fff;
	padding: 20px;
}

@-webkit-keyframes App-logo-spin {
  	from { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
  	to { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
}

@keyframes App-logo-spin {
  	from { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
  	to { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
}



